import { Col, Row } from "react-bootstrap";
import "./About.css";
import datum from "../../Assets/data";
const About = () => {
  return (
    <>
      <Row className="row-content row-scrollspy">
        <Col sm={12} md={4}>
          <div
            id="simple-list-example"
            class="d-flex flex-column gap-2 simple-list-example-scrollspy text-center position-sticky"
          >
            {datum.map((data) => (
              <a key={data.id} class="p-1 rounded" href={`#${data.title}`}>
                {data.title}
              </a>
            ))}
          </div>
        </Col>
        <Col sm={12} md={8}>
          <div
            data-bs-spy="scroll"
            data-bs-target="#simple-list-example"
            data-bs-offset="0"
            data-bs-smooth-scroll="true"
            class="scrollspy-example"
            tabindex="0"
          >
            {datum.map((data) => (
              <>
                <h4 id={`${data.title}`}>{data.title}</h4>
                <div class="card mb-3">
                  <div class="row g-0">
                    <div class="col-md-4">
                      <img
                        src={data.path}
                        class="img-fluid rounded-start"
                        alt={`sales funnel steps ${data.title}`}
                      />
                    </div>
                    <div class="col-md-8">
                      <div class="card-body">
                        <h5 class="card-title">{data.objective}</h5>
                        <p class="card-text">
                          {data.description}
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </>
            ))}
          </div>
        </Col>
      </Row>
    </>
  );
};

export default About;
